.main-container {
  width: 100%;
  height: 100%;
}

.image-loop {
  position: relative;
}

.image-loop img {
  position: absolute;
  width: 830px;
  top: 0;
  left: 20%;
  opacity: 0;
  animation: dissolve 37.5s infinite;
  overflow: hidden;
}

/* .image-loop img:nth-child(1) {
  animation-delay: 3s;
} */

.image-loop img:nth-child(2) {
  animation-delay: 0s; 
}

.image-loop img:nth-child(3) {
  animation-delay: 5s; 
}

.image-loop img:nth-child(4) {
  animation-delay: 10s; 
}

.image-loop img:nth-child(5) {
  animation-delay: 15s; 
}

.image-loop img:nth-child(6) {
  animation-delay: 20s; 
}

.image-loop img:nth-child(7) {
  animation-delay: 25s;
}

.image-loop img:nth-child(1) {
  animation-delay: 30s; 
}

/* Continue adding nth-child selectors and adjusting animation-delay for the remaining images */

@keyframes dissolve {
  0% {
    opacity: 0;
  }
  4% {
    opacity: 1;
  }
  20% {
    opacity: 1;
  }
  36% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}